import { interpolate, toText } from "./.fable/fable-library.3.1.1/String.js";
import { singleton } from "./.fable/fable-library.3.1.1/AsyncBuilder.js";
import { awaitPromise } from "./.fable/fable-library.3.1.1/Async.js";
import { fetch$ } from "./.fable/Fable.Fetch.2.3.1/Fetch.fs.js";
import { ofSeq, empty } from "./.fable/fable-library.3.1.1/List.js";
import { empty as empty_1, singleton as singleton_1, collect, delay } from "./.fable/fable-library.3.1.1/Seq.js";
import { date as date_1, parse } from "./.fable/fable-library.3.1.1/Date.js";
import { isWeekDay } from "./Utils.fs.js";
import { Msg, Event$, DayType } from "./Types.fs.js";
import { Cmd_OfAsync_start, Cmd_OfAsyncWith_perform } from "./.fable/Fable.Elmish.3.1.0/cmd.fs.js";

export function holidayFile(year) {
    return toText(interpolate("holidays/holidays-%P().json", [year]));
}

export function fetchHolidayData(year) {
    return singleton.Delay(() => singleton.Bind(awaitPromise(fetch$(toText(interpolate("/%P()", [holidayFile(year)])), empty())), (_arg1) => singleton.ReturnFrom(awaitPromise(_arg1.text()))));
}

export function parseHolidayData(text) {
    return ofSeq(delay(() => collect((event) => {
        let date;
        let copyOfStruct = parse(event["date"]);
        date = date_1(copyOfStruct);
        return isWeekDay(date) ? singleton_1(new Event$(date, date, event["name"], new DayType(3))) : empty_1();
    }, JSON.parse(text))));
}

export function fetchHolidays(year) {
    return singleton.Delay(() => singleton.Bind(fetchHolidayData(year), (_arg1) => singleton.Return([year, parseHolidayData(_arg1)])));
}

export function Fetch(year) {
    return Cmd_OfAsyncWith_perform((x) => {
        Cmd_OfAsync_start(x);
    }, fetchHolidays, year, (tupledArg) => (new Msg(1, tupledArg[0], tupledArg[1])));
}

